.ChartType {
	margin-bottom:32px 0;

}

.ChartType-btn {
	width: 100px;
	height: 100px;
	margin: 12px;
	border: 1px solid #dbdbdb;
	border-radius: 16px;

	background-size: 90% 90%;
	background-color: rgba(0,0,0,0);
	background-position: center center;
	background-repeat: no-repeat;
}

.ChartType-btn:hover {
	cursor:pointer;
}



