.Chart {
	max-width:1100px;
	margin:0 auto;
	position:relative;
}

.Chart-main-content {
	display:block;
}

.Chart-btn {
	z-index: 10;
	position: absolute;
	top: 0;
	right: 0;
	padding: 12px;
	border: none;
	font-weight: bold;
	font-family: sans-serif;
	font-size: 1.2rem;
	border-radius: 12px;
	background-color: #222;
	color: white;
}
.Chart-btn:hover {
	cursor:pointer;
}

.Chart-error {
	font-size:0.8rem;
	color:red;
}



