html {
	height:100%;
}
body {
	min-height:100%;
	position:relative;
	margin: 0;
	padding: 0;
	font-family: sans-serif;
	padding-bottom:400px;
}

.footer {
	width:100%;
	height:210px;
	position:absolute;
	bottom:0;
	left:0;

	background-color:#222222;
	color:white;
	padding:12px;
}
.footer-element {
	margin:6px 0;
	font-size:0.8rem;
}
.footer em {
	font-weight: bold;
}



